.page {
  font-family: "Inter", sans-serif;
  scroll-behavior: smooth;
  font-size: 16px;
  color: #292929;

  &__body {
    margin: 0;

    &--with-menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(-100%);
    transition: transform 0.3s;
      
    &:target {
      transform: translate(0);
    }
  }
  
  &__section {
    margin-bottom: 120px;
  }
  
  &__section--title {
    font-weight: 700;
    font-size: 32px;
    line-height: 32px;
    text-align: center;
    letter-spacing: -1.5px;
    margin: 0 0 48px;

    @include onTablet {
      font-size: 40px;
      line-height: 100%;
      margin-bottom: 64px;
    }
     
    @include onDesktop {
      font-size: 48px;
      line-height: 48px;
      margin-bottom: 64px;
    }
  }
}
